import React, {Component} from 'react';
import ReactSelectForm from './ReactSelectForm';
import {
  Row,Form, FormGroup, FormControl, InputGroup, DropdownButton, MenuItem, ControlLabel
} from 'react-bootstrap';

class EmployerForm extends Component {
  constructor(props){
    super(props);
  }

  render(){
    const selectOptions = [
      { value: 'currents', label: 'Currents' },
      { value: 'formers', label: 'Formers' },
      { value: 'both', label: 'Both' }
    ];

    return(
      <Row className="employer-form-container">
        <Form inline className="add-margin-to-form">
        <InputGroup>
          <ControlLabel className="advanced-search-label">EMPLOYER</ControlLabel>
          <FormControl
            value={this.props.currentEmployerBinding}
            onChange={(e)=>{this.props.bindFormInputToState('CurrentEmployer',e.currentTarget.value)}}
            type="text" placeholder="Employer" />
          <ReactSelectForm
            bindFormInputToState={this.props.bindFormInputToState}
            onChangeValue={'CurrentEmployer'}
            customClasses="employer-select-form"
            selectPlaceholder="Currents, Formers, Both"
            selectOptions={selectOptions}
            multi={false} />
        </InputGroup>
        </Form>
      </Row>
    );
  }
}

export default EmployerForm;



