import React, {Component} from 'react';
import Select from 'react-select';
import ReactSelectForm from './ReactSelectForm';
import {
  Row,Form, FormGroup, FormControl, InputGroup, DropdownButton, MenuItem, ControlLabel
} from 'react-bootstrap';

const filterHelper = {
  getSelectedOption: (state) => {
    const { selectedOption } = state;
  	return selectedOption || selectedOption.value;
  }
};

class TitleForm extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectedOption: ''
    }
  }

  render(){
    const selectOptions = [
      { value: 'currents', label: 'Currents' },
      { value: 'formers', label: 'Formers' },
      { value: 'both', label: 'Both' }
    ];
    return(
      <Row className="title-form-container">
        <Form inline className="add-margin-to-form">
        <InputGroup>
          <ControlLabel className="advanced-search-label">TITLE</ControlLabel>
          <FormControl
            value={this.props.titleBinding}
            onChange={(e)=>{this.props.bindFormInputToState('JobTitle',e.currentTarget.value)}}
            type="text" placeholder="Title" />
          <ReactSelectForm
            bindFormInputToState={this.props.bindFormInputToState}
            onChangeValue={'JobTitleRadio'}
            customClasses="title-select-form"
            selectPlaceholder="Currents, Formers, Both"
            selectOptions={selectOptions}
            multi={false} />
        </InputGroup>
        </Form>
      </Row>
    );
  }
}

export default TitleForm;



