import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import DefaultSearch from './DefaultSearch.js';
import DefaultSearchOptions from './DefaultSearchOptions.js';
import AdvancedSearch from './AdvancedSearch.js';
import SearchResult from './SearchResult';
import Toolbelt from './Toolbelt';
import {Grid} from 'react-bootstrap';
import axios from 'axios';
import './css/App.css';
import 'react-select/dist/react-select.css';


class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      headersApiEndpoint: 'https://jt-apid.guidepoint.io/app_dev.php/auth',
      searchApiEndpoint: 'https://jt-apid.guidepoint.io/app_dev.php/legacy_api',
      headerData: {},
      hidden: true,
      keyCodes: [],
      firstNameRef: '',
      searchFormData: {
        query: '',
        nohighlight: 0,
        FirstName: '',
        LastName: '',
        JobTitle: '',
        JobTitleRadio: 'Current',
        CurrentEmployer: '',
        IncludeChildren: 0,
        ExcludeCurrent: 0,
        ShortBio: '',
        country: '',
        setDefaultSector: 0,
        industry: '',
        businessType: '',
        ScreeningQuestions: '',
        isDNC: 0,
        pending: 0,
        optedOut: 0,
        noEmployerConsent: 0,
        requiresTranslator: 0,
        excludeP: 0,
        excludeUnclassified: 0,
        excludePrivate: 0,
        excludeGovernment: 0
      },
      searchResults: [],
      loadingResults: false,
      noResultsFound: false,
      showMinimizedDashboard: !this.hidden || this.searchResults.length
    };

  }

  toggleAdvancedSearch(){
    this.setState({
      hidden: !this.state.hidden
    });
    return false;
  }

  bindFormInputToState(inputName, newValue){
    let searchFormClone = this.state.searchFormData;
    if(typeof searchFormClone[inputName] !== 'undefined'){
      searchFormClone[inputName] = newValue;
      this.setState({ searchFormData: searchFormClone });
    } else {
      console.error(`${inputName} is not a valid payload property.`);
    }
  }

  setupHotKeys(){
    document.addEventListener('keyup', (e) => {
      const resetKeyCodes = () => { this.setState({keyCodes: []}); };
      const firstNameFormNode = ReactDOM.findDOMNode(this.state.firstNameRef);

      // console.log('keycode ', e.keyCode);
      // Shift 16, Enter 13, Tab 9
      if(e.keyCode == 16 || e.keyCode == 13 || e.keyCode == 9){
        this.setState({
          keyCodes: this.state.keyCodes.concat(e.keyCode)
        })
      }

      if(this.state.keyCodes.length >= 2 &&
        this.state.keyCodes[0] === 16 &&
        this.state.keyCodes[1] === 16){
        const hiddenState = !this.state.hidden;

        this.setState({ hidden: hiddenState });
        resetKeyCodes();

        if(!hiddenState){
          firstNameFormNode.focus();
        }
      } else if(this.state.keyCodes.length >= 2 &&
        this.state.keyCodes.indexOf(16) !== -1 &&
        this.state.keyCodes.indexOf(13) !== -1 &&
        !this.state.hidden){
        this.makeSearch();
      } else if(this.state.keyCodes.length >= 2 &&
        this.state.keyCodes.indexOf(16) !== -1 &&
        this.state.keyCodes.indexOf(9) !== -1){
        this.resetSearch();
        firstNameFormNode.focus();
      }
      setTimeout(resetKeyCodes, 300);
    });
  }

  resetSearch(){
    this.setState({
      searchFormData: {
        query: '',
        nohighlight: 0,
        FirstName: '',
        LastName: '',
        JobTitle: '',
        JobTitleRadio: 'Current',
        CurrentEmployer: '',
        IncludeChildren: 0,
        ExcludeCurrent: 0,
        ShortBio: '',
        country: '',
        setDefaultSector: 0,
        industry: '',
        businessType: '',
        ScreeningQuestions: '',
        isDNC: 0,
        pending: 0,
        optedOut: 0,
        noEmployerConsent: 0,
        requiresTranslator: 0,
        excludeP: 0,
        excludeUnclassified: 0,
        excludePrivate: 0,
        excludeGovernment: 0
      },
      searchResults: [],
      loadingResults: false,
      noResultsFound: false
    });
  }

  componentWillMount(){
    this.setupHotKeys();
  }

  getFirstNameFormRef(r){
    this.setState({
      firstNameRef: r
    });
    return false;
  }

  getHeaders(){
    let data = new FormData();
    data.append('_username', 'dev');
    data.append('_password', 'newfeb');

    const getHeadersRequest = () => {
      return axios({
        method: 'post',
        url: this.state.headersApiEndpoint,
        headers: {
          'Accept': 'application/vnd.guidepoint.app+json;version=1.0',
          'Content-Type': 'multipart/form-data'
        },
        data: data
      });
    };

    return new Promise((resolve,reject) => {
      // Nice Idea, caching header data, just need to find a good logical flow for it.
      // if(Object.keys(this.state.headerData).length){
      //   resolve(this.state.headerData);
      // }

      getHeadersRequest().then(response =>{
        this.setState({ headerData: response });
        resolve(response);
      }).catch(error => {
        reject(error);
      });

    });
  }

  makeSearch(){
    if(this.state.loadingResults){
      return false;
    }

    this.setState({
      loadingResults: true,
      hidden: true
    });

    this.getHeaders().then(headers => {
      // console.log('headers returned: ', 'headers ', headers,`bearer ${headers.data.data.token}`);
      axios({
        method: 'post',
        headers: {
          Accept:'application/vnd.Guidepoint.app_+json',
          Authorization: `Bearer ${headers.data.data.token}`
        },
        url: this.state.searchApiEndpoint,
        data:{
          "phpsessid": "6nhgado8465iv2813oenlh70l7",
          "path": "facet/getSearchResults/0",
          "crmUrl" : "jt3.crm.guidepointglobal.com",
          "body": this.state.searchFormData
        }
      }).then((searchResponse) => {

        if(typeof searchResponse.data.data.Contents.data.data.response !== 'undefined'){
          this.constructData(searchResponse.data.data.Contents.data.data.response.docs);
        } else {
          // no results
          this.setState({
            noResultsFound: true,
            loadingResults: false
          });
        }

      }).catch(error => {
        console.error('Error getting search results', error);
      });

    }).catch((error) => {
      console.error("Error getting headers: ", error);
    });

  }

  constructData(searchResponse){
    let searchResults = [];
    searchResponse.forEach(advisor => {
      const noDescriptionText = "No Description Entered."
      if(!advisor._short_bio){
        advisor._short_bio = "";
      }

      searchResults.push({
       name: advisor._full_name ,
       location: `${advisor._city} ${advisor._state} ${advisor._country} ${advisor._business_type_facet}`,
       bio: advisor._short_bio.length > 500 ? `${advisor._short_bio.slice(0,500)}...` : (advisor._short_bio.length ? advisor._short_bio : noDescriptionText),
       long_bio: advisor._short_bio.length ? advisor._short_bio : noDescriptionText,
       extended_bio: advisor._short_bio && advisor._short_bio.length > 500 ? true : false,
       contactInfo: {
           'Primary Email':	advisor._e_mail,
           'Secondary Email': advisor._e_mail2,
           'Tertiary Email':	'None',
           'Personal Phone': advisor._phone1,
           'Mobile Phone': advisor._phone1,
           'Phone':	advisor._phone2,
           'Timezone': advisor._time_zone,
           'Primary Language':  'None',
           'Secondary Language':	'None',
           'Requires Translator':	advisor._requires_translator,
           'LinkedIn URL':	'None',
           'Compliance Conflicts':	'None'
       },
       details: {
          'Consultation Rate':	'350 USD',
          '1x1 Meeting Rate':	'0 USD',
          'Group Meeting Rate':	'0 USD',
          'Teleconference Rate':	'0 USD',
          'Minimum Billing Time':	'NA',
          'Joined Network':	'09/22/2009',
          'Last Contacted':	'04/23/2012',
          'Last Replied':	'04/24/2012',
          'Last Consulted':	'09/17/2010',
          'Number of Paid Invoices':	'1',
          'Org\'s Projects Past 3 Mo\'s':	'0',
          'Org\'s Projects Past 6 Mo\'s':	'0',
          'Org\'s Projects Past 12 Mo\'s':	'0',
          'All Projects Past 3 Mo\'s':	'0',
          'All Projects Past 6 Mo\'s':	'0',
          'All Projects Past 12 Mo\'s':	'0'
       }});
    });

    this.setState({
      searchResults,
      loadingResults: false
    });
  }

  render() {
    const appIsProcessing = !this.state.hidden || (this.state.searchResults.length && !this.state.loadingResults);
    // console.log(this.state.showMinimizedDashboard);
    return (
      <div className="app-container">
        <div className="App search-component">
          <Grid>
            <div className="background-overlay"></div>
            <DefaultSearch
              makeSearch={this.makeSearch.bind(this)}
              searchFormData={this.state.searchFormData}
              bindFormInputToState={this.bindFormInputToState.bind(this)}
              />
            <DefaultSearchOptions
              hidden={this.state.hidden}
              resetSearch={this.resetSearch.bind(this)}
              toggleAdvancedSearch={this.toggleAdvancedSearch.bind(this)}
              />
            <AdvancedSearch
              hidden={this.state.hidden}
              searchFormData={this.state.searchFormData}
              getFirstNameFormRef={this.getFirstNameFormRef.bind(this)}
              bindFormInputToState={this.bindFormInputToState.bind(this)}
              />
            <SearchResult
              noResultsFound={this.state.noResultsFound}
              searchResults={this.state.searchResults}
              loadingResults={this.state.loadingResults}
              list={this.state.advisorList}  />
          </Grid>
          </div>
          <Toolbelt appIsProcessing={appIsProcessing} />
      </div>
    );
  }
}

export default App;



