import React, { Component } from 'react';
import {Row, Col, Checkbox, Button, Glyphicon} from 'react-bootstrap';
import './css/DefaultSearchOptions.css';


class DefaultSearchOptions extends Component {
  constructor(props){
    super(props);
  }

  render(){

    return(
      <Row className="default-search-options">
        <Col md={4} smHidden xsHidden>
          <p><span className="current-request-text">Current Request:</span> #218478 ePayAdvisorPortal</p>
        </Col>
        <Col md={4} mdOffset={0} className="large-screen-search-checkboxes" xsHidden smHidden>
          <Checkbox inline>
      			Hit Highlighting
      		</Checkbox>
          <Checkbox inline>
      			Qualify
      		</Checkbox>
        </Col>
        <Col md={4} className="search-buttons">
          <Button inline bsSize="small" className="reset-button" onClick={this.props.resetSearch}>
            Reset
          </Button>
          <Button inline bsSize="small" bsStyle={this.props.hidden ? 'default' : 'guidepoint'} className="advanced-search-dropdown"
            onClick={this.props.toggleAdvancedSearch}>
            Advanced Search
            <Glyphicon glyph="chevron-down" style={{display: this.props.hidden ? 'none' : 'inline'}}/>
            <Glyphicon glyph="chevron-right" style={{display: this.props.hidden ? 'inline' : 'none'}}/>
        </Button>
        </Col>
        <Col md={4} mdOffset={0} className="small-screen-search-checkboxes" mdHidden lgHidden>
          <Checkbox inline>
      			Hit Highlighting
      		</Checkbox>
          <Checkbox inline>
      			Qualify
      		</Checkbox>
        </Col>
      </Row>
    );
  }
}

export default DefaultSearchOptions;



