/**** DUMMY COMPONENT FOR COPY/PASTE **/
import React, {Component} from 'react';
import Select from 'react-select';

const filterHelper = {
  getSelectedOption: (state) => {
    const { selectedOption } = state;
  	return selectedOption || selectedOption.value;
  }
};

class ReactSelectForm extends Component {
  constructor(props){
    super(props);

    this.state = {
      selectedOption: '',
      selectOptions: this.props.selectOptions
    };
  }

  handleChange = (selectedOption) => {
    selectedOption = selectedOption ? selectedOption : '';
    this.setState({ selectedOption });
    // console.log('this.state.selectOption',this.state.selectOption)
    // this.props.bindFormInputToState(
    //   this.props.onChangeValue,
    //   this.props.multi ? this.state.selectOptions : this.state.selectedOption.value);
  }

  render(){

    return(
      <Select
       className={`${this.props.customClasses}`}
       name="form-field-name"
       placeholder={this.props.selectPlaceholder}
       value={filterHelper.getSelectedOption(this.state)}
       multi={this.props.multi}
       onChange={this.handleChange}
       options={this.state.selectOptions}
     />
    );
  }
}

export default ReactSelectForm;



