import React, { Component } from 'react';
import {Row, Col, Checkbox, ToggleButton, ToggleButtonGroup, Nav, NavItem} from 'react-bootstrap';
import '../css/SearchResult.css';
import Contact from './Contact';
import ActivityDetail from './ActivityDetail';

class SearchResultCard extends Component {
  constructor(props){
    super(props);

    this.state = {
        checked: false,
        showContent: {
            bio: true,
            employment: false,
            contact: false,
            detail: false
        },
        checkboxTest: false,
        long_bio: false
    }
  }

    getContent(){
        if(this.state.showContent.contact){
            return <Contact contactInfo = {this.props.contactInfo} />;
        } else if(this.state.showContent.detail){
            return <ActivityDetail details = {this.props.details} />;
        } else if (this.state.showContent.employment) {
            return <div> Employment history</div>;
        } else {
            return (
              <div>
                <p className="advisor-location">{this.props.location}</p>
                {this.state.long_bio ? this.props.longBio : this.props.bio}
              </div>
            );
        }
    }

    onToggleTab (value) {
        const newShowContent = {};
        for (const [key, objValue] of Object.entries(this.state.showContent)) {
            newShowContent[key] = (key===value);
        }
        this.setState({showContent:newShowContent});
    }


    checkboxChange () {
        this.state.checked = !this.state.checked;
        this.props.onSelectChange(this.props.id, this.state.checked);
    }
    render(){
      let dynamicClasses = [
        this.props.extendedBio && !this.state.long_bio ? 'extended-bio' : ''
      ];

        return(
            <Col md={6}>
                <div className="search-result-card" onClick={e => { console.log('clicked!'); this.setState({long_bio: true}); }}>
                    <div className={`search-result-card-container ${dynamicClasses.map(c => c)}`}>
                        <Row className="search-result-card-header">
                        <Col xs={5} sm={4}>
                            <Checkbox onChange={()=>this.checkboxChange()}>
                                <span className="advisor-name">{this.props.name}</span>
                            </Checkbox>
                        </Col>
                        <Col xs={7} sm={8} >
                            <Nav className="button-group" bsStyle="tabs" pullRight onSelect={value => this.onToggleTab(value)}>
                                <NavItem eventKey={'bio'} active={this.state.showContent.bio}>Bio</NavItem>
                                <NavItem eventKey={'employment'} active={this.state.showContent.employment}>Employment</NavItem>
                                <NavItem eventKey={'contact'} active={this.state.showContent.contact}>Contact</NavItem>
                                <NavItem eventKey={'detail'} active={this.state.showContent.detail}>Detail</NavItem>
                            </Nav>
                        </Col>
                        </Row>
                        <Row>
                        <div className="card-content">
                        {this.getContent()}
                        </div>
                        </Row>
                    </div>
                </div>
            </Col>
    );
  }
}

export default SearchResultCard;



