import React, {Component} from 'react';
import NameForm from './AdvancedSearchComponents/NameForm';
import TitleForm from './AdvancedSearchComponents/TitleForm';
import EmployerForm from './AdvancedSearchComponents/EmployerForm';
import IncludeExcludeOptions from './AdvancedSearchComponents/IncludeExcludeOptions';
import {IncludeAdvisors, ExcludeAdvisors, RefineSearch } from './AdvancedSearchComponents/Filters';
import { BiographyForm, CountryForm, SectorForm,
  IndustryForm, BusinessTypeForm, ScreeningQuestionsForm } from './AdvancedSearchComponents/DetailForms';
import './css/AdvancedSearch.css';
import {
  Row, Col, Checkbox
} from 'react-bootstrap';


class AdvancedSearch extends Component {
  constructor(props){
    super(props);

    this.state = {
      firstNameForm: "",
      lastNameForm: "",
      titleForm: {},
      employerForm: {},
      biographyForm: "",
      countryForm: "",
      sectorForm: "",
      industryForm: "",
      businessTypeForm: "",
      screeningQuestionsForm: ""
    }
  }

  render(){
    const conditionalClasses = {
      display: this.props.hidden ? 'none' : 'block'
    };
    const sfd = this.props.searchFormData;

    return (
      <Row className="advanced-search-container" style={conditionalClasses}>
        <Col md={6}>
          <NameForm
            firstNameBinding={sfd.FirstName}
            lastNameBinding={sfd.LastName}
            getFirstNameFormRef={this.props.getFirstNameFormRef}
            bindFormInputToState={this.props.bindFormInputToState} />
          <TitleForm
            titleBinding={sfd.JobTitle}
            titleRadioBinding={sfd.JobTitleRadio}
            bindFormInputToState={this.props.bindFormInputToState}/>
          <EmployerForm
            currentEmployerBinding={sfd.CurrentEmployer}
            bindFormInputToState={this.props.bindFormInputToState}/>
          <IncludeExcludeOptions bindFormInputToState={this.props.bindFormInputToState}/>
        </Col>
        <Col md={6}>
          <BiographyForm
            shortBioBinding={sfd.ShortBio}
            bindFormInputToState={this.props.bindFormInputToState}/>
          <CountryForm
            countryBinding={sfd.country}
            bindFormInputToState={this.props.bindFormInputToState}/>
          <SectorForm
            bindFormInputToState={this.props.bindFormInputToState}/>
          <IndustryForm bindFormInputToState={this.props.bindFormInputToState}/>
          <BusinessTypeForm bindFormInputToState={this.props.bindFormInputToState}/>
          <ScreeningQuestionsForm
            screeningQuestionsBinding={sfd.ScreeningQuestions}
            bindFormInputToState={this.props.bindFormInputToState}/>
          <IncludeAdvisors bindFormInputToState={this.props.bindFormInputToState}/>
          <ExcludeAdvisors bindFormInputToState={this.props.bindFormInputToState}/>
          <RefineSearch bindFormInputToState={this.props.bindFormInputToState}/>
        </Col>
      </Row>
    );
  }
}

export default AdvancedSearch;



