import React, {Component} from 'react';
import {
  Row,Checkbox
} from 'react-bootstrap';

class IncludeExcludeOptions extends Component {
  constructor(props){
    super(props);
  }

  render(){
    return(
      <Row>
        <Checkbox
          onChange={(e)=>{this.props.bindFormInputToState('IncludeChildren',e.currentTarget.checked)}}>
           Include Advisors of Immediate Subsidiaries
        </Checkbox>
        <Checkbox
          onChange={(e)=>{this.props.bindFormInputToState('ExcludeCurrent',e.currentTarget.checked)}}>
          Exclude Advisors with same Current Employer
        </Checkbox>
      </Row>
    );
  }
}

export default IncludeExcludeOptions;



