import React, {Component} from 'react';
import {
  Row,FormGroup, InputGroup, Button, FormControl, ControlLabel
} from 'react-bootstrap';

class BiographyForm extends Component {
  constructor(props){
    super(props);
  }

  render(){
    return(
      <Row>
  			<FormGroup>
          <ControlLabel className="advanced-search-label">BIOGRAPHY</ControlLabel>
  				<FormControl
            value={this.props.shortBioBinding}
            onChange={(e)=>{this.props.bindFormInputToState('ShortBio',e.currentTarget.value)}}
            type="text" placeholder="Biography.."/>
  			</FormGroup>
      </Row>
    );
  }
}

class CountryForm extends Component {
  constructor(props){
    super(props);
  }

  render(){
    return(
      <Row>
        <FormGroup>
  				<ControlLabel className="advanced-search-label">COUNTRY</ControlLabel>
          <FormControl
            onChange={(e)=>{this.props.bindFormInputToState('country',e.currentTarget.value)}}
            componentClass="select" placeholder="select" className="detail-select-form">
    				<option value="select">select</option>
    				<option value="other">...</option>
    			</FormControl>
  		</FormGroup>
      </Row>
    );
  }
}

