import React, {Component} from 'react';
import '../css/SideScroller.css';

class SideScroller extends Component {
  constructor(props){
    super(props);
  }

  renderSideScrollerItems(){
    // TODO: convert ID to using dateSearched when / if the real data comes in
    const itemProperties = [];
    return this.props.items.map(searchItemProperties => {
      return (
        <li key={searchItemProperties.id}>
          <p className="date-searched">{searchItemProperties.date}</p>
          { Object.keys(searchItemProperties).map(key => {
            if(key !== 'id' && key !== 'date'){
              return (
                <p key={`${key}-${searchItemProperties.id}`}>
                  <span className={`search-key search-key-${key}`}>{key.toUpperCase()}: </span>
                  <span className={`search-val search-val-${searchItemProperties[key]}`}>{searchItemProperties[key]}</span>
                </p>
              );
            }
          }) }
        </li>
      );
    });
  }

  render(){
    return(
      <div className="side-scroller-container">
        <p className="scroller-label">{this.props.label}</p>
        <div className="fader fader-right"></div>
        <div className="fader fader-left"></div>
        <ul className="side-scroller">
          {this.renderSideScrollerItems()}
        </ul>
      </div>
    );
  }
}

export default SideScroller;



