import React, { Component } from 'react';
import {Row, Col, Button} from 'react-bootstrap';
import SearchResultCard from './SearchResultCard/SearchResultCard';
import gpLoader from './img/gploader.jpg';
import './css/SearchResult.css';

class SearchResult extends Component {
  constructor(props){
    super(props);
    this.state = {
        selected: []
    }
  }

  // TODO: make changes according to JSON response
  // rewrite render() in Contact.js and ActivityDetail.js
  // rewrite how to handle selected


  onSelectChange (selectedIndex, isSelected) {
      // strucutre of this.state.selected will change according to submit request payload
      this.state['selected'][selectedIndex] = isSelected;
      console.log('selectle', this.state.selected);
  }

  clicked() {
      console.log('selected', this.state.selected)
  }

  render(){
    let showResults = this.props.searchResults.length && !this.props.loadingResults;

    if(this.props.loadingResults){
      return(
        <div className="gp-loader-container">
          <img className="gp-loader-icon" src={gpLoader} alt="gploader" />
        </div>
      );
    }

    if(this.props.noResultsFound){
      return (
        <div className="no-results-found">
          <h1>No Results Found</h1>
          <p>Try searching for another advisor</p>
        </div>
      );
    }

    return(
        <Row className="search-result" style={{ display: showResults ? 'block' : 'none'}}>
            { this.props.searchResults.map((result, index) => {
                return (<SearchResultCard
                    onSelectChange={ (selectedIndex, isSelected) => this.onSelectChange(selectedIndex, isSelected) }
                    key={index}
                    id={index}
                    name={result.name}
                    location= {result.location}
                    bio={result.bio}
                    contactInfo={result.contactInfo}
                    longBio={result.long_bio}
                    extendedBio={result.bio.length > 500}
                    details= {result.details}/>)
                })
              }
            <Button className="result-submit-button" onClick= {()=>this.clicked()}> Submit </Button>
        </Row>

    );
  }
}

export default SearchResult;



