import React, {Component} from 'react';
import SideScroller from './ToolbeltComponents/SideScroller';
import {recentSearchData, currentProjects} from './mockData';

import { Glyphicon, Grid, Row, Col } from 'react-bootstrap';
import './css/Toolbelt.css';

class Toolbelt extends Component {
  constructor(props){
    super(props);

    this.state = {
      expandedDashboardOpen: true,
      fsDashboardOpen: false,
      showMinimizedDashboard :false,
      appIsProcessing: this.props.appIsProcessing,
      recentSearches: recentSearchData,
      pmNotifications: currentProjects
    };
  }

  hideExpandedDashboard(e){
    this.setState({
      expandedDashboardOpen: false,
      showMinimizedDashboard : true
    });
  }

  showExpandedDashboard(){
    this.setState({
      expandedDashboardOpen: true,
      showMinimizedDashboard : false,
      fsDashboardOpen : this.props.appIsProcessing ? true : false
    });
  }

  componentWillUpdate(nextProps, nextState){
    if( nextProps.appIsProcessing && (nextProps.appIsProcessing !== this.props.appIsProcessing) ){
      this.hideExpandedDashboard();
    }
  }

  renderDash(){
    if(this.state.showMinimizedDashboard){
      return (
        <div onClick={this.showExpandedDashboard.bind(this)} className="toolbelt-minimized">
          Dashboard
          <Glyphicon className="toolbar-glyph" glyph="dashboard" />
        </div>
      );
    } else {
      return (
        <div className={`toolbelt-expanded ${this.state.fsDashboardOpen ? "full-screen" : ""}`}>
          <Grid fluid>
            <Row>
              <div className="toolbelt-expanded-header">
                <h3 className="toolbelt-expanded-title">Dashboard</h3>
                <Glyphicon onClick={ this.hideExpandedDashboard.bind(this) } className="toolbelt-expanded-arrow" glyph="chevron-down"/>
              </div>
            </Row>
            <Row>
              <SideScroller label="RECENT SEARCHES" items={this.state.recentSearches.recentSearches}/>
            </Row>
            <Row>
              <SideScroller label="CURRENT PROJECTS" items={this.state.pmNotifications}/>
            </Row>
          </Grid>
        </div>
      );
    }
  }

  render(){
    return(
      <div className="toolbelt-container">
        { this.renderDash() }
      </div>
    );
  }
}

export default Toolbelt;



