/**** DUMMY COMPONENT FOR COPY/PASTE **/

import React, {Component} from 'react';
import { Row, ControlLabel } from 'react-bootstrap';
import ReactSelectForm from './ReactSelectForm';

class IncludeAdvisors extends Component {
  constructor(props){
    super(props);
    this.state = {
      includeAdvisorOptions: [
        { value: 'dnc', label: 'DNC' },
        { value: 'pending', label: 'Pending' },
        { value: 'optedOut', label: 'Opted Out' },
        { value: 'noEmployerConsent', label: 'No Employer Consent' },
        { value: 'requiresTranslator', label: 'Requires Translator' }
      ]
    }
  }

  render(){
    return(
      <Row className="filter-select">
      <ControlLabel className="advanced-search-label">INCLUDE ADVISORS</ControlLabel>
        <ReactSelectForm
          customClasses="add-margin-to-form"
          selectPlaceholder="DNC, Pending, Opted Out, No Employer Consent, Requires Translator"
          selectOptions={this.state.includeAdvisorOptions}
          multi={true} />
      </Row>
    );
  }
}

class ExcludeAdvisors extends Component {
  constructor(props){
    super(props);
    this.state = {
      excludeAdvisorOptions:[
        { value: 'publicCompanies', label: 'Public Companies' },
        { value: 'unclassifiedCompanies', label: 'Unclassified Companies' },
        { value: 'privateCompanies', label: 'Private Companies' },
        { value: 'government', label: 'Government' }
      ]
    }
  }

  render(){
    return(
      <Row className="filter-select">
      <ControlLabel className="advanced-search-label">EXCLUDE ADVISORS</ControlLabel>
     <ReactSelectForm
       customClasses="add-margin-to-form"
       selectPlaceholder="Public Companies, Unclassified Companies, Private Companies, Government"
       selectOptions={this.state.excludeAdvisorOptions}
       multi={true} />
      </Row>
    );
  }
}

class RefineSearch extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectedOption: '',
      refineSearchOptions: [
        { value: 'one', label: 'Filters With Categories Go Here' }
      ]
    }
  }

  render(){
    return(
      <Row className="filter-select">
        <ControlLabel className="advanced-search-label">REFINE SEARCH</ControlLabel>
        <ReactSelectForm
         customClasses="add-margin-to-form"
         selectPlaceholder="Refine Search Filters.."
         selectOptions={this.state.refineSearchOptions}
         multi={true} />
      </Row>
    );
  }
}

export { IncludeAdvisors, ExcludeAdvisors, RefineSearch };



