
import React, { PropTypes } from 'react'
import { Row, Col, Thumbnail } from 'react-bootstrap'

const Contact = props => (

    <Row className="contact-detail">
        {props.contactInfo?
            Object.entries(props.contactInfo)
                .map(([key, value],index) =>
                (<Col md={6} key={index}>{key}: {value}</Col>))
            :''
        }
    </Row>
);

export default Contact;



