import React, { Component } from 'react';
import {Row, Col, FormGroup, InputGroup,
  FormControl, DropdownButton, MenuItem, Button, SplitButton} from 'react-bootstrap';
import logo from './img/guidepoint-logo.svg';
import './css/DefaultSearch.css';


class DefaultSearch extends Component {
  constructor(props){
    super(props);
    this.state = {
      searchFilterOption: "All"
    };
  }

  handleSearch(e){
    e.preventDefault();
    this.props.makeSearch();
  }

  render(){
    return (
      <Row className="default-search-container">
          <Col md={4} className="left-label">
            <h2><img className="search-logo" src={logo} alt="guidepoint"/></h2>
            <p className="current-request-text-mobile">
              <span className="current-request-text">Current Request:</span> #218478 ePayAdvisorPortal
            </p>
          </Col>

          <Col md={8}>
            <form onSubmit={this.handleSearch.bind(this)}>
              <FormGroup className="default-search">
          			<InputGroup>
                  <InputGroup.Button>
                    <DropdownButton
                      bsSize="small"
                      bsStyle="default"
                      id="search-category"
                      inputRef={node => this.searchFilterNode = node}
                      componentClass={InputGroup.Button}
                      onSelect={ eventKey => this.setState({searchFilterOption: eventKey}) }
                      title={this.state.searchFilterOption}>
                      <MenuItem eventKey="All">All</MenuItem>
                      <MenuItem eventKey="Employees">Employees</MenuItem>
                      <MenuItem eventKey="Companies">Companies</MenuItem>
                    </DropdownButton>
                  </InputGroup.Button>

          				<FormControl
                    inputRef={node => this.defaultSearchNode = node}
                    type="text"
                    bsSize="small"
                    placeholder="Employee Name, Company Name..."
                    value={this.props.searchFormData.query}
                    onChange={(e) => {this.props.bindFormInputToState('query',e.currentTarget.value);}}/>
                  <InputGroup.Button>
                    <Button
                      className="btn-guidepoint"
                      onClick={this.handleSearch.bind(this)}
                      bsSize="small"
                      id="search-button">Search</Button>
                  </InputGroup.Button>
          			</InputGroup>
          		</FormGroup>

            </form>
          </Col>

      </Row>
    );
  }
}

export default DefaultSearch;



