import React, {Component} from 'react';
import {
  Row,Form, FormGroup, FormControl, InputGroup, ControlLabel
} from 'react-bootstrap';

class NameForm extends Component {
  constructor(props){
    super(props);
  }

  componentDidMount(){
    console.log('name refs: ', this.refs);
  }

  componentDidMount(){
    this.props.getFirstNameFormRef(this.refs.firstNameForm);
    return false;
  }

  render(){
    return(
      <Row className="name-form-container">
        <Form inline className="add-margin-to-form">
        <InputGroup>
          <ControlLabel className="advanced-search-label">NAME</ControlLabel>
          <FormControl
            onChange={(e)=>{this.props.bindFormInputToState('FirstName',e.currentTarget.value)}}
            ref="firstNameForm"
            type="text"
            placeholder="First Name"
            value={this.props.firstNameBinding}
            className="first-name-form" />
        </InputGroup>
        <InputGroup>
          <FormControl
            onChange={(e)=>{this.props.bindFormInputToState('LastName',e.currentTarget.value)}}
            className="label-adjuster"
            type="text"
            placeholder="Last Name"
            value={this.props.lastNameBinding}
            className="last-name-form"/>
        </InputGroup>
        </Form>
      </Row>
    );
  }
}

export default NameForm;



